.overlay {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    background: rgba(1, 1, 1, 0.8);
    justify-content: center;
    z-index: 200;
  }
  
  .modal {
    display: flex;
    flex-direction: column;
    width: 560px;
    padding: 0;
    height: 280px;
    background: white;
    outline: none;
    border: 1px solid #424242;
  }
  
  .header {
    padding: 16px 0;
  }
  
  .closeBtn {
    background: transparent;
    outline: none;
    border: none;
    position: absolute;
    top: 4px;
    right: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    width: 32px;
  }
  
  .closeBtn:hover {
    background: #dedede;
  }
  
  .content {
    text-align: center;
  
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.5px;
    text-align: center;
  }
  
  .title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 1.25px;
    text-align: center;
    text-transform: uppercase;
    gap: 16px;
  }